:root {
  --Azul-oscuro: rgba(0, 26, 51, 1);
  --Azul-claro: rgba(102, 153, 204, 1);
  --Verde-oscuro: rgba(0, 102, 51, 1);
  --Verde-claro: rgba(153, 204, 102, 1);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

